import React from 'react';
import '../styles/components.css';

/**
 * 
 * @param {Object} props - 
 * @param {Function} props.onClear - 
 * @param {Function} props.onLoadSample - 
 * @param {Object} props.stats - 
 * @returns {JSX.Element} 
 */
const Header = ({ onClear, onLoadSample, stats }) => {
  return (
    <header className="header">
      <div className="header-content">
        <h1 className="header-title">Markdown Previewer</h1>
        <div className="header-actions">
          <button 
            className="btn btn-secondary" 
            onClick={onLoadSample}
            title=""
          >
            
          </button>
          <button 
            className="btn btn-primary" 
            onClick={onClear}
            title=""
          >
            
          </button>
        </div>
      </div>
      {stats && (
        <div className="header-stats">
          <span className="stat-item">
            : <strong>{stats.charCount}</strong>
          </span>
          <span className="stat-item">
            : <strong>{stats.lineCount}</strong>
          </span>
          {stats.lintStats && (
            <span className="stat-item">
              : <strong>{stats.lintStats.total}</strong>
              {stats.lintStats.errors > 0 && (
                <span className="error-count"> (: {stats.lintStats.errors})</span>
              )}
            </span>
          )}
        </div>
      )}
    </header>
  );
};

export default Header;
